export default {
    props: ['meInform'],
    data() {
        return {
            voice: false,  // 声音
            shake: false,  // 震动
            alarm: false,   //异常警报
            exceed: false,   //超时提醒
            vocies: [0, 1, 2, 3],  // 播报次数列表
            section: 0,   // 播报次数默认1次
            show: false,   // 播报次数弹窗显示
            PositionValue: null,    //  当操作失败的时候  返回的提示信息
            showPosition: false,   //  控制操作失败时弹窗显示或隐藏
        }
    },
    mounted() {
        this.getInfo();
    },
    methods: {
        // 初始化页面数据
        getInfo() {
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.getting,
                type: "post",
                data: {},
                success: (response, allResponse) => {
                    // console.log('response',response.voice_num)
                    this.voice = this.isTrue(response.is_sound)
                    this.shake = this.isTrue(response.is_shock)
                    this.alarm = this.isTrue(response.is_abnormal)
                    this.exceed = this.isTrue(response.time_out)
                    this.section = response.voice_num
                }
            });
        },
        // 默认数据转化
        isTrue($data) {
            if (typeof($data) == 'boolean') {
                if ($data === true) return 1;
                return 0;
            } else {
                if ($data == 1) return true;
                return false;
            }
        },
        // 保存按钮
        baocun() {
            console.log("我要提交保存了..")
            console.log('声音' + this.voice)
            console.log('震动' + this.shake)
            console.log('异常警报' + this.alarm)
            console.log('超时提醒' + this.exceed)
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.edit,
                type: "post",
                data: {
                    is_sound: this.isTrue(this.voice),
                    is_shock: this.isTrue(this.shake),
                    is_abnormal: this.isTrue(this.alarm),
                    time_out: this.isTrue(this.exceed),
                    voice_num: this.isTrue(this.section),
                },
                success: (response, allResponse) => {
                    // console.log('response', allResponse.msg)
                    this.showPosition = true;
                    this.PositionValue = allResponse.msg;
                    setTimeout(()=>{
                        this.$router.go(0)
                    },900)
                }
            });
        },
        // 选择语音播报次数弹窗
        vocieNum() {
            this.show = true
        },
        // 选择语音播报次数弹窗=>确定按钮
        onConfirm() {
            console.log("我点击了确定", this.section)
        }
    }
}



