    export default {
        props: ['meDetails'],
        data() {
            return {
                PositionValue: null,    //  当操作失败的时候  返回的提示信息
                showPosition: false,   //  控制操作失败时弹窗显示或隐藏
                userInfo: {
                    avatar: 'https://gss0.bdstatic.com/-4o3dSag_xI4khGkpoWK1HF6hhy/baike/c0%3Dbaike92%2C5%2C5%2C92%2C30/sign=62d46c39067b020818c437b303b099b6/d4628535e5dde7119c3d076aabefce1b9c1661ba.jpg'
                },   //  用户头像
                sexList: ['男','女'],
                birthday: "请选择您的生日",  // 生日时间
                where: '',        // 城市
                valueName: '',       //  名字
                signature: '',      //  签名
                sexNum: 0,          //  判断性别   0男1女
                imgcode: null,    //  头像路径
            }
        },
        mounted(){
            this.getInfo();
        },
        methods:{
            // 数据初始化
            getInfo(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getDetails,
                    type: "post",
                    data: {

                    },
                    success: (response, allResponse) => {
                        // console.log('response',response)
                        this.where = response.city;
                        this.valueName = response.username;
                        this.signature = response.signature;
                        this.birthday = response.birthday;
                        this.sexNum = response.sex;
                        this.userInfo.avatar = response.head_img;
                        this.imgcode = response.head_img;
                    }
                });
            },
            // 将头像显示以及头像后台提交
            handleFile(e){
                let $target = e.target || e.srcElement;
                let file = e.target.files[0];
                var reader = new FileReader();
                reader.onload = (data) => {
                    let res = data.target || data.srcElement;
                    this.userInfo.avatar = res.result;
                };
                reader.readAsDataURL(file);
                let param = new FormData(); //创建form对象
                param.append('file',file,file.name);//通过append向form对象添加数据
                let config = {
                    headers:{'Content-Type':'multipart/form-data'}
                };  //添加请求头
                this.$ajax.post(this.common.apiUrl.uploadImg,param,config)
                    .then(response=>{
                        // console.log(response.data.data);
                        this.imgcode = response.data.data.src;
                        // console.log(this.imgcode)
                    })
            },
//            选择性别
            gender(index){
                this.sexNum = index
                console.log(this.sexNum)
            },
            // 提交保存
            upSave(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.userBaseEdit,
                    type: "post",
                    data: {
                        birthday: this.birthday,
                        nickname: this.valueName,
                        sex: this.sexNum,
                        signature: this.signature,
                        head_img: this.imgcode,
                        city: this.where
                    },
                    success: (response, allResponse) => {
                        // console.log('allResponse',allResponse.msg)
                        this.showPosition = true;
                        this.PositionValue = allResponse.msg;
                        setTimeout(()=>{
                            this.$router.go(0)
                        },900)
                    }
                });
            },
//            选择生日
            onConfirm(){
                console.log("我选择的生日时间是",this.birthday)
            },
        }
    }



