    import personalDetails from '@/components/mine/personalDetails';
    import personalInform from '@/components/mine/personalInform';
    import suggestion from '@/components/mine/suggestion';
    export default {
        components: {
            personalDetails,
            personalInform,
            suggestion,
        },
        data() {
            return {
                info: null,
                list1: [['当前状态','空闲', '忙碌', '离开']],  // 可选服务状态
                value1: ['空闲'],  //当前服务状态
                personalDetails: false,   // 个人信息弹窗
                meDetails: [],    // 个人信息选项卡
                personalService: false,   // 客服弹窗
                personalInform: false,   // 通知设置弹窗
                meInform: [],   // 通知设置选项卡
                feedback: false,   // 意见反馈弹窗,
                mefeedback: [],   // 意见反馈选项卡
                setCenter: false,  //设置中心弹窗
                aboutMessage: false, // 设置中心弹窗=>关于我们弹窗
                work_status: null,
                Cid:window.localStorage.getItem('Cid'),  // 用户AppCid
            }
        },
        mounted(){
            this.getInfo();
        },
        methods:{
            outzou(){
                const _this = this;
                this.$vux.confirm.show({
                    title: '提示',
                    content: '确定退出？',
                    onConfirm () {
                        _this.common.commonFun.ajax({
                            dom: _this,
                            url: _this.common.apiUrl.LoginOut,
                            type: "post",
                            data: {
                                user_id: window.localStorage.getItem("user_id")
                            },
                            success: (response, allResponse) => {
                                // 显示
                                _this.$vux.loading.show({
                                    text: '加载中'
                                });
                                localStorage.removeItem('access_token');
                                localStorage.removeItem('user_id');
                                localStorage.removeItem('statistics');
                                _this.$vux.loading.hide();
                                _this.$router.push('/login')
                            }
                        });

                    }
                })
            },
            getInfo(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getMine,
                    type: "post",
                    data: {

                    },
                    success: (response, allResponse) => {
                        this.info = response;
                        if(response.work_status == 0){
                            this.value1[0] = '离开'
                        }
                        if(response.work_status == 1){
                            this.value1[0] = '空闲'
                        }
                        if(response.work_status == 2){
                            this.value1[0] = '忙碌'
                        }
                    }
                });
            },
//            扫描图标点击是触发
            camera(){
                this.$router.push("/testCode")
                console.log("我可能要扫描二维码了")
            },
//            改变服务状态是触发
            onChange () {
                if(this.value1[0] == '空闲'){
                    this.work_status = 1
                }
                if(this.value1[0] == '离开'){
                    this.work_status = 0
                }
                if(this.value1[0] == '忙碌'){
                    this.work_status = 2
                }
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.editWorkStatus,
                    type: "post",
                    data: {
                        work_status: this.work_status
                    },
                    success: (response, allResponse) => {
                        // console.log('response',response)
                    }
                });
            },
//            个人信息弹窗
            details (){
                this.personalDetails = true
            },
//            客服弹窗
            service(){
                this.personalService = true
            },
//            客服弹窗确定按钮
            onConfirm(){
                console.log("我确定拨打电话了")
            },
//            通知设置
            inform(){
                this.personalInform = true
            },
//            意见反馈弹窗
            opinion(){
                this.feedback = true
            },
//            设置中心弹窗
            set(){
                this.setCenter = true
            },
//            设置中心=>清除缓存按钮
            cache(){
                console.log("我要清除缓存了")
            },
//            设置中心=>关于我们按钮
            aboutme(){
                this.aboutMessage = true
                console.log("关于我们")
            },
//            我的服务
            meServe(){
                this.$router.push('/mine/mineServe')
            },
//            我的打赏
            meTip(){
                this.$router.push('/mine/mineComplaint')
            },
//            被投诉的
            meComplaint(){
                this.$router.push('/mine/mineTip')
            }
        }
    }



