    export default {
        data() {
            return {
                value: '',   //  反馈类型
                valueNum: null,  //  反馈类型下标
                feedbackList: ['考勤管理', '人力管理', '呼叫服务', '报表统计', '产品体验', '产品功能', '其它'],   // 反馈类型列表
                placeholder: "请写下你对云速订的感受,我们将认真听取你的意见,努力提供更优质的服务力.",   //  反馈意见提示文本
                feedbackText: "",  // 反馈意见
                phone: "",  // 手机号码
                imgList: [],     //  图面预览列表
                imgcode: [],    //  通过请求后天之后返回的头像路径
                limit:6, //限制图片上传的数量
                options: {
                    getThumbBoundsFn (index) {
                        // find thumbnail element
                        let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]
                        // get window scroll Y
                        let pageYScroll = window.pageYOffset || document.documentElement.scrollTop
                        // optionally get horizontal scroll
                        // get position of element relative to viewport
                        let rect = thumbnail.getBoundingClientRect()
                        // w = width
                        return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
                        // Good guide on how to get element coordinates:
                        // http://javascript.info/tutorial/coordinates
                    }
                },
                PositionValue: null,    //  当操作失败的时候  返回的提示信息
                showPosition: false,   //  控制操作失败时弹窗显示或隐藏
            }
        },
        methods:{
//            选择异常列表值
            onChange() {
                this.feedbackList.forEach((item,index)=>{
                    if(this.value == item){
                        this.valueNum = index
                    }
                })
            },
//            提交
            submit(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.setFeedback,
                    type: "post",
                    data: {
                        type: this.valueNum,
                        remark: this.feedbackText,
                        contact: this.phone,
                        img: this.imgcode
                    },
                    success: (response, allResponse) => {
                        // console.log('allResponse',allResponse.msg)
                        this.showPosition = true;
                        this.PositionValue = allResponse.msg;
                        setTimeout(()=>{
                            this.$router.go(0)
                        },900)
                    }
                });
            },
            // 点击图片进行放大观看
            magnfiy (index) {
                console.log(this.imgList)
                this.$refs.previewer.show(index)
            },
            // 点击上传图片
            fileChange(e){
                let file = e.target.files[0];
                var reader = new FileReader();
                reader.onload = (data) => {
                    let res = data.target || data.srcElement;
                };
                reader.readAsDataURL(file);
                let param = new FormData(); //创建form对象
                param.append('file',file,file.name);//通过append向form对象添加数据
                let config = {
                    headers:{'Content-Type':'multipart/form-data'}
                };  //添加请求头
                this.$ajax.post(this.common.apiUrl.feedback,param,config)
                    .then(response=>{
                        // console.log(response.data.data);
                        this.imgList.push({
                            msrc: response.data.data.src,
                            src: response.data.data.src
                        })
                        this.imgcode.push(response.data.data.src)
                        console.log(this.imgcode)
                    })
            },
            // 点击图片右上角的X按钮进行相应的删除
            delImg(index) {
                this.imgList.splice(index, 1);
                this.imgcode.splice(index, 1);
                if (this.limit !== undefined) this.limit = 6-this.imgList.length;
            },
        }
    }



